.modal-container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 579px;
    height: 465px;
    // height: 98vh;
    // display: flex;
    // flex-direction: column;
    // justify-content: space-between;
    margin: 0 auto;
    padding: 40px;
    overflow: auto;
    border: 2px solid #111111;
    background-color: #FFFFFF;
}
.modal-book {
    display: flex;
    gap: 24px;
    justify-content: space-between;
}

.modal-close__btn-wrapper {
    position: absolute;
    top: 18px;
    right: 18px;
    cursor: pointer;
  }

  .modal-close-btn {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background-color: transparent;
    border: 0;
    opacity: 0.3;
    cursor: pointer;
    &:hover {
      opacity: 1;
    }
  }

  .modal-book__img-container {
    margin-bottom: 30px;
    overflow: hidden;
    border-radius: 18px;
    max-width: 192px;
    height: 281px;
  }

  .modal-book__img {
    overflow: hidden;
  border-radius: 5px;
  width: 192px;
  }

  .modal-book__description {
    width: 279px;
   
  }

  .modal-book__title {
    font-size: 24px;
    line-height: 28px;

  }

  .modal-book__author {
    font-size: 14px;
    line-height: 18px;
    color: #B4AFAF;
  }
  .modal-book__about {
    width: 279px;
    height: 70px;
    font-size: 14px;
    line-height: 18px;
    color: #111111;
  }

  .modal-book__button {
    width: 499px;
    padding: 20px 149px;
    border-radius: 40px;
    font-size: 18px;
    line-height: 24px;
    color: #111111;
    background-color: #FFFFFF;
  }
  .modal-book__stores {
    margin-top: 28px;
  }